<template>
  <div class>
    <div class="gloss">***以下图片均来自互联网（The following images are from the Internet）***</div>
    <div id="container">
      <div class="box" v-for="(item, index) in imgData.data" :key="index">
        <div class="box_img">
          <img :src="item.src" alt />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 模拟数据
      imgData: {
        data: []
      },
      img: {
        data: [
          {
            "src": require("../assets/img/QQ图片20170316160757.png"),
            "w": 1920,
            "h": 1080,
          },
          {
            "src": require("../assets/img/QQ图片20170115163333.png"),
            "w": 636,
            "h": 581,
          },
          {
            "src": require("../assets/img/d4bbcfe06fbfa4830422bfb69647c3c68bb932bc.jpg"),
            "w": 594,
            "h": 779,
          },
          {
            "src": require("../assets/img/243c04f6236254826fcf5fc98b63832d3b150c76.jpg"),
            "w": 585,
            "h": 780,
          },
          {
            "src": require("../assets/img/QQ图片20170327222525.png"),
            "w": 413,
            "h": 574,
          },
          {
            "src": require("../assets/img/9df03d05gy1fdubomad3bj20pk0zu0wp.jpg"),
            "w": 690,
            "h": 968,
          },
          {
            "src": require("../assets/img/3bbb8053gy1fe02txccv2j20bq0kudhh.jpg"),
            "w": 422,
            "h": 750,
          },
        ]
      }
    };
  },
  beforeMount() {
    for (let i = 0; i < 5; i++) {
      this.imgData.data.push(...this.img.data);
    }
  },
  mounted () {
    window.onload = () => {
      this.imgLocation("container", "box");
    }
    // 监听滚动条
    // let that = this;
    // window.onscroll = function () {
    //   if (that.checkFlag()) {
    //     console.log(111)
    //     let cparent = document.getElementById("container");
    //     for (let i = 0; i < that.img.data.length; i++) {
    //       that.imgData.data.push(that.img.data[i])
    //     }
    //     that.imgLocation("container", "box")
    //   }
    // }
  },
  methods: {
    // 图片的加载
    imgLocation (parent, content) {
      let cparent = document.getElementById(parent);
      // 盒子的个数
      let ccontent = this.getChildElement(cparent, content);
      // 每张图片的宽度
      let imgWidth = ccontent[0].offsetWidth;
      // 一行多少图
      let num = Math.floor(document.documentElement.clientWidth / imgWidth);
      // 瀑布流容器宽度及居中
      cparent.style.cssText = "width:" + imgWidth * num + "px;margin: 0 auto";
      // 承载第一行所有图片的高度
      let boxHeightArr = [];
      for (let i = 0; i < ccontent.length; i++) {
        // 只承载第一行的高度
        if (i < num) {
          ccontent[i].style.position = "absolute";
          ccontent[i].style.top = "0px";
          ccontent[i].style.left = imgWidth * i + "px";
          boxHeightArr[i] = ccontent[i].offsetHeight;
        } else {
          //取第一行中高度的最小值
          let minHeight = Math.min.apply(null, boxHeightArr);
          // 最小高度值的位置
          let minIndex = this.getMinHeightLocation(boxHeightArr, minHeight);
          // 图片的位置
          ccontent[i].style.position = "absolute";
          ccontent[i].style.top = minHeight + "px";
          ccontent[i].style.left = ccontent[minIndex].offsetLeft + "px";
          boxHeightArr[minIndex] = boxHeightArr[minIndex] + ccontent[i].offsetHeight;
        }
      }
    },
    // 得到最小高度图片的位置
    getMinHeightLocation (boxHeightArr, minHeight) {
      for (let i in boxHeightArr) {
        if (boxHeightArr[i] == minHeight) {
          return i;
        }
      }
    },
    // 得到图片信息
    getChildElement (parent, content) {
      let contentArr = [];
      let allcontent = parent.getElementsByTagName("*");
      // 将信息储存到数组中
      for (let i = 0; i < allcontent.length; i++) {
        if (allcontent[i].className == content) {
          contentArr.push(allcontent[i]);
        }
      }
      return contentArr;
    },
    // 图片的无限加载
    checkFlag () {
      let cparent = document.getElementById("container");
      // 得到具体内容
      let ccontent = this.getChildElement(cparent, "box");
      // 最后一张图片距顶部的高度
      let lastContentHeight = ccontent[ccontent.length - 1].offsetTop;
      // 滑动的距离
      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
      // 浏览器的高度
      let pageHeight = document.documentElement.clientHeight || document.body.clientHeight;
      if (lastContentHeight < scrollTop + pageHeight) {
        return true;
      }
    }
  },
  watch: {},
  computed: {},
}
</script>
<style scoped>
</style>


